.modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
  }
  
  .modalContent {
    z-index: 2;
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    border-radius: 10%;
    width: 500px;
    font-style: Roboto;
  }
  
  .modalContent__wrapper {
    display: flex;
  }
  
  .modalContent__description {
    display: flex;
    flex-direction: column;
    min-width: 200px;
    font-weight: 500;
  }
  
  .modalContent h3 {
    padding-left: 5px;
  }
  
  .modalContent__description span {
    padding: 7px;
  }
  
  .modalContent__data {
    display: flex;
    flex-direction: column;
  }
  
  
  .modalContent__data span:nth-child(1) {
    font-weight: 600;
  }
  
  .modalContent__data span:nth-child(2) {
    padding-top: 10px;
  }
  
  .modalContent__closeBtn {
    position: absolute;
    margin-left: 520px;
    margin-top: -57px;
    width: 50px;
    cursor: pointer;
  }
  
  .modalContent__additionalInfo {
    display: flex;
    justify-content: space-around;
  }

  .modalContent__additionalInfo span {
    padding-top: 20px;
    color: gray;
    font-size: 0.8rem;
  }